import React from "react";

interface InputProps {
  id: string;
  type: string;
  placeholder: string;
  icon: string;
  value: string;
  onChange: (e: React.ChangeEvent<HTMLInputElement>) => void;
}

const Input: React.FC<InputProps> = ({ id, type, placeholder, icon, value, onChange }) => {
  return (
    <div className="relative">
      <input
        id={id}
        type={type}
        placeholder={placeholder}
        value={value}
        onChange={onChange}
        className="w-full px-4 py-3 text-sm text-neutral-600 placeholder:text-neutral-600 border-b border-neutral-600 focus:outline-none focus:border-primary-color transition"
      />
      <img
        src={icon}
        alt=""
        className="absolute left-3 top-1/2 transform -translate-y-1/2 w-5 h-5"
      />
    </div>
  );
};

export default Input;
