"use client";

import React, { useState } from "react";
import AuthService from "./AuthService";
import Button from "@/components/UI/Button";
import Input from "@/components/UI/Input";
import LoginError from "./LoginError";

const LoginForm: React.FC = () => {
  const [username, setUsername] = useState("");
  const [password, setPassword] = useState("");
  const [error, setError] = useState<string | null>(null);

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    setError(null);
    try {
      await AuthService.login(username, password);
      alert("Inicio de sesión exitoso");
    } catch (err: any) {
      setError(err.message);
    }
  };

  return (
    <form onSubmit={handleSubmit} className="space-y-4">
      {/* Títulos */}
      <h1 className="text-primary-900 text-3xl font-black">¡Bienvenido de nuevo!</h1>
      <h2 className="text-neutral-600 text-xl font-medium mb-6">Welcome Back!</h2>

      {/* Inputs */}
      <div className="mb-4">
        <Input
          id="username"
          type="text"
          placeholder="Ingresa tu correo o tu usuario"
          icon="/assets/email-icon.svg"
          value={username}
          onChange={(e) => setUsername(e.target.value)}
        />
      </div>
      <div className="mb-4">
        <Input
          id="password"
          type="password"
          placeholder="Ingresa tu contraseña"
          icon="/assets/lock-icon.svg"
          value={password}
          onChange={(e) => setPassword(e.target.value)}
        />
      </div>

      {/* Checkbox y link */}
      <div className="flex items-center justify-between">
        <label className="flex items-center text-sm text-neutral-600">
          <input type="checkbox" className="form-checkbox text-primary-900 mr-2" />
          Recordar por 30 días
        </label>
        <a href="#" className="text-primary-900 text-sm font-medium hover:underline">
          ¿Olvidaste tu contraseña?
        </a>
      </div>

      {/* Botón y error */}
      {error && <LoginError message={error} />}
      <Button type="submit">Iniciar sesión</Button>
    </form>
  );
};

export default LoginForm;
